/* ---------------------------------- */
/* Keywords Input
------------------------------------- */
.keywords-container {
  margin-bottom: -15px;
}

.keywords-list {
  display: block;
  float: left;
  height: 0;
}

.keyword {
  background-color: rgba(42, 65, 232, 0.07);
  color: #2a41e8;
  display: inline-block;
  float: left;
  padding: 0;
  border-radius: 7px;
  cursor: default;
  margin: 0 7px 7px 0;
  height: 35px;
  line-height: 35px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-animation: KeywordIn 0.3s ease-in-out;
  animation: KeywordIn 0.3s ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  overflow: hidden;
  max-width: 100%;
  font-size: 14.7px;
}

.keyword.keyword-removed {
  max-width: 0;
  margin: 0;
  overflow: hidden;
  opacity: 0;
  -webkit-animation: KeywordOut 0.4s ease-in-out;
  animation: KeywordOut 0.4s ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  pointer-events: none;
}

@-webkit-keyframes KeywordIn {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes KeywordIn {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@-webkit-keyframes KeywordOut {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    margin-top: -35px;
  }
}

@keyframes KeywordOut {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    margin-top: -35px;
  }
}

.keyword-text {
  padding-right: 12px;
}

