/* ---------------------------------- */
/* Input animation placeholder
------------------------------------- */
.line {
  position: relative;
  height: 55px;
}

.line__input {
  border: 1px solid #dadce0 !important;
  font-size: 15px;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 4px;
  outline: 0;
  width: 100%;
  height: 100%;
  padding: 0 16px;
  padding-top: 11px !important;
  height: 55px !important;
}

.line__placeholder {
  position: absolute;
  z-index: 1;
  pointer-events: none;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  left: 15px;
  font-size: 15px;
  padding: 0 5px;
  color: #a7a7a7;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}

.line__input:focus {
  -webkit-box-shadow: 0 0 0 1px #2a41e8 inset;
  box-shadow: 0 0 0 1px #2a41e8 inset;
  border-color: transparent !important;
}

.line textarea {
  padding-top: 22px !important;
}

.line__input:not([value=""]) + .line__placeholder,
.line__input:not([value=""]) + .line__placeholder,
.line__input:focus + .line__placeholder {
  top: 6px;
  font-size: 12px;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.line__input:focus + .line__placeholder {
  color: #2a41e8;
}

.line textarea + .line__placeholder {
  top: 30px;
}

/* ---------------------------------- */
/* Checkbox
------------------------------------- */
.checkbox {
  display: inline-block;
  margin-bottom: 0;
}

.checkbox input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}

.checkbox label {
  position: relative;
  cursor: pointer;
  padding-left: 30px;
  line-height: 25px;
}

.checkbox label span.checkbox-icon {
  content: "";
  -webkit-appearance: none;
  background-color: transparent;
  border: 2px solid #b4b4b4;
  height: 20px;
  width: 20px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 5px;
  border-radius: 7px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  position: absolute;
  left: 0;
  top: 1px;
}

.checkbox input + label span.checkbox-icon:after {
  position: absolute;
  top: 2px;
  left: 0;
  right: 0;
  margin: 0 auto;
  opacity: 0;
  content: "";
  display: block;
  width: 6px;
  height: 10px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  -webkit-transform: scale(0.5) rotate(45deg);
  transform: scale(0.5) rotate(45deg);
  -webkit-transition: all 0.35s cubic-bezier(0.3, -0.41, 0.19, 2), opacity 0.3s;
  transition: all 0.35s cubic-bezier(0.3, -0.41, 0.19, 2), opacity 0.3s;
}

.checkbox input:checked + label span.checkbox-icon {
  border-color: #2a41e8;
  background-color: #2a41e8;
}

.checkbox input:checked + label span.checkbox-icon:after {
  -webkit-transform: scale(1) rotate(45deg);
  transform: scale(1) rotate(45deg);
  opacity: 1;
}

