.cd-secondary-nav {
  position: relative;
  z-index: 1;
  clear: both;
  width: 100%;
  height: 50px;
  /* Force Hardware Acceleration */
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  will-change: transform;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.cd-secondary-nav::afterr {
  /* gradient on the right - to indicate it's possible to scroll */
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  height: 100%;
  width: 35px;
  background: transparent;
  background: -webkit-gradient(
    linear,
    right top,
    left top,
    from(#25283d),
    to(rgba(37, 40, 61, 0))
  );
  background: linear-gradient(to left, #25283d, rgba(37, 40, 61, 0));
  pointer-events: none;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.cd-secondary-nav.nav-end::after {
  opacity: 0;
}

.cd-secondary-nav ul,
.cd-secondary-nav li,
.cd-secondary-nav a {
  height: 100%;
}

.cd-secondary-nav ul {
  /* enables a flex context for all its direct children */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 15px;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.cd-secondary-nav ul::after {
  clear: both;
  content: "";
  display: block;
}

.cd-secondary-nav li {
  display: inline-block;
  float: left;
  /* do not shrink - elements float on the right of the element */
  -ms-flex-negative: 0;
  flex-shrink: 0;
}

.cd-secondary-nav li:last-of-type {
  padding-right: 20px;
}

.cd-secondary-nav li.active a,
.cd-secondary-nav li.uk-active a {
  border-bottom: 3px solid #2a41e8 !important;
}

.cd-secondary-nav li.active a,
.cd-secondary-nav li.uk-active a,
.cd-secondary-nav li:hover a {
  color: #2a41e8 !important;
}

.cd-secondary-nav a {
  display: block;
  line-height: 50px;
  font-weight: 500;
  color: #8c8d90;
  font-size: 15px;
}

.cd-secondary-nav a span {
  padding: 5px 9px;
  background: rgba(42, 65, 232, 0.08);
  border-radius: 30px;
  font-weight: 500;
  font-size: 12px;
  margin-left: 3px;
  color: #2a41e8;
  line-height: 0.75rem;
}

.cd-secondary-nav.is_light li a {
  color: #ffffff9c;
}

.cd-secondary-nav.is_light li.active a,
.cd-secondary-nav.is_light li.uk-active a,
.cd-secondary-nav.is_light li:hover a {
  color: white;
}

.cd-secondary-nav.is_light li.active a,
.cd-secondary-nav.is_light li.uk-active a {
  border-bottom: 3px solid white;
}

.cd-secondary-nav.extanded ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
}

.cd-secondary-nav.extanded ul li {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 0%;
  flex: 1 1 0%;
  padding: 0 !important;
}

.cd-secondary-nav.extanded ul li a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.cd-secondary-nav.nav-small {
  height: 57px !important;
}

.cd-secondary-nav.nav-small li a {
  line-height: 55px !important;
}

@media only screen and (min-width: 1024px) {
  .cd-secondary-nav {
    height: 68px;
    overflow: visible;
  }
  .cd-secondary-nav ul {
    /* reset mobile style */
    display: block;
    padding: 0;
  }
  .cd-secondary-nav li {
    /* reset mobile style */
    float: none;
    -ms-flex-negative: 1;
    flex-shrink: 1;
  }
  .cd-secondary-nav a {
    line-height: 68px;
