  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

.breadcrumb-area {
  position: relative;
  padding: 25px 0;
}

.breadcrumb-area .breadcrumb ul li a {
  line-height: initial;
}

.breadcrumb-area h2 {
  margin-top: 0.375rem;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 600;
}

.breadcrumb {
  padding: 0;
  margin: 0;
}

.breadcrumb ul {
  width: 100%;
}

.breadcrumb ul li {
  display: inline-block;
}

.breadcrumb ul li:after {
  content: "\e930";
  color: #434343;
  padding: 0 2px;
  display: inline-block;
  font-size: 14px;
  font-family: "Feather-Icons";
}

.breadcrumb ul li a {
  font-weight: 500;
  font-size: 14px;
  line-height: 70px;
  color: #434343;
}

.breadcrumb ul li:last-child:after {
  display: none;
}

.breadcrumb ul li.active a {
  color: #889999;
}

.breadcrumb.text-white a,
.breadcrumb.text-white li::after {
  color: rgba(255, 255, 255, 0.7);
}

.breadcrumb.text-white .active a {
  color: white;
}

/* -------------------------------------------------------------- */
/* 04. Responsive
----------------------------------------------------------------- */
.btn-night-mode .btn-night-mode-switch {
  display: inline-block;
  height: 17.5px;
  width: 36px;
  top: 35%;
  right: 13px;
  position: absolute;
}

.btn-night-mode .uk-switch-button {
  background-color: rgba(0, 0, 0, 0.09);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  border-radius: 500px;
  bottom: 0;
  cursor: pointer;
  -webkit-transition-property: background-color;
  transition-property: background-color;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.07);
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.07);
}

.btn-night-mode .uk-switch-button:before {
  content: "";
  background-color: #a9a9a9;
  position: absolute;
  width: 20px;
  height: 20px;
  left: 0px;
  bottom: -1px;
  border-radius: 50%;
  -webkit-transition-property: -webkit-transform, -webkit-box-shadow;
  transition-property: -webkit-transform, -webkit-box-shadow;
  transition-property: transform, box-shadow;
  transition-property: transform, box-shadow, -webkit-transform,
    -webkit-box-shadow;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
}

.night-mode .btn-night-mode .uk-switch-button {
  background-color: #4e4e4e !important;
}

.night-mode .btn-night-mode .uk-switch-button::before {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
  -webkit-box-shadow: 0 0 6px rgba(0, 0, 0, 0.22);
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.22);
  -webkit-transform: translateX(17px);
  transform: translateX(17px);
  background: #62d76b;
}
